.wrapper {
    margin: 0 auto
}

.events-table {
    margin-top: 5%;
    margin-bottom: 5%;
}

.events-table div {
    padding: 0.3em;
}

.events-table .table-header {
    position: sticky;
    top: 0;
    background-color: #d0d0d0;
    border-bottom: 1px solid #4c4c4c;
}

.events-table .line {
    display: flex;
}

.events-table .line .counters {
    display: inline-block;
}

.events-table .line .event-name {
    display: inline-block;;
}

.events-table .line .level-1 h6 {
    font-weight: 650;
    padding: 1em 1em 0 1em;
}

.events-table .line .level-2 {
    padding-left: 3em;
}

.events-table .line .level-3 {
    padding-left: 5em;
}

.events-table .line .counters .counter {
    min-width: 3em;
    text-align: center;
    vertical-align: bottom;
}

